import JSZip from "jszip";

/**
 * Êñá‰ª∂Â§ÑÁêÜÂ∑•ÂÖ∑Á±ª
 */
class FileUtil {
  /**
   * Ê†ºÂºèÂåñÊñá‰ª∂Â§ßÂ∞è
   * @param {number} bytes Â≠óËäÇÊï∞
   * @returns {string} Ê†ºÂºèÂåñÂêéÁöÑÊñá‰ª∂Â§ßÂ∞è
   */
  static formatBytes(bytes) {
    if (bytes === 0) return '0 Bytes';
    
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  }
  
  /**
   * Ëé∑ÂèñÊñá‰ª∂Êâ©Â±ïÂêç
   * @param {string} filename Êñá‰ª∂Âêç
   * @returns {string} Êâ©Â±ïÂêç
   */
  static getFileExtension(filename) {
    return filename.slice((filename.lastIndexOf('.') - 1 >>> 0) + 2);
  }

  /**
   * Ëé∑ÂèñÊñá‰ª∂Êâ©Â±ïÂêçÔºàÂÖºÂÆπFileServiceÁöÑgetExtÊñπÊ≥ïÔºâ
   * @param {string} filename Êñá‰ª∂Âêç
   * @returns {string} Êâ©Â±ïÂêç
   */
  static getExt(filename) {
    return filename.split('.').pop().toLowerCase();
  }
  
  /**
   * Ëé∑ÂèñÊñá‰ª∂ÂêçÔºà‰∏çÂê´Êâ©Â±ïÂêçÔºâ
   * @param {string} filename Êñá‰ª∂Âêç
   * @returns {string} ‰∏çÂê´Êâ©Â±ïÂêçÁöÑÊñá‰ª∂Âêç
   */
  static getFileNameWithoutExtension(filename) {
    return filename.replace(/\.[^/.]+$/, '');
  }

  /**
   * Ëé∑ÂèñÊñá‰ª∂Âü∫Á°ÄÂêçÔºàÂÖºÂÆπFileServiceÁöÑgetBaseNameÊñπÊ≥ïÔºâ
   * @param {string} filename Êñá‰ª∂Âêç
   * @returns {string} ‰∏çÂê´Êâ©Â±ïÂêçÁöÑÊñá‰ª∂Âêç
   */
  static getBaseName(filename) {
    const basename = filename.lastIndexOf('.') === -1
      ? filename // Â¶ÇÊûúÊ≤°ÊúâÊâ©Â±ïÂêçÔºåËøîÂõûÂéüÂßãÊñá‰ª∂Âêç
      : filename.substring(0, filename.lastIndexOf('.'));
    return basename;
  }
  
  /**
   * Ê£ÄÊü•ÊòØÂê¶‰∏∫ÂõæÁâáÊñá‰ª∂
   * @param {string} filename Êñá‰ª∂Âêç
   * @returns {boolean} ÊòØÂê¶‰∏∫ÂõæÁâáÊñá‰ª∂
   */
  static isImageFile(filename) {
    const imageExtensions = ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp', 'svg'];
    const extension = this.getFileExtension(filename).toLowerCase();
    return imageExtensions.includes(extension);
  }
  
  /**
   * Ê£ÄÊü•ÊòØÂê¶‰∏∫ËßÜÈ¢ëÊñá‰ª∂
   * @param {string} filename Êñá‰ª∂Âêç
   * @returns {boolean} ÊòØÂê¶‰∏∫ËßÜÈ¢ëÊñá‰ª∂
   */
  static isVideoFile(filename) {
    const videoExtensions = ['mp4', 'avi', 'mov', 'wmv', 'flv', 'webm', 'mkv'];
    const extension = this.getFileExtension(filename).toLowerCase();
    return videoExtensions.includes(extension);
  }
  
  /**
   * Ê£ÄÊü•ÊòØÂê¶‰∏∫Èü≥È¢ëÊñá‰ª∂
   * @param {string} filename Êñá‰ª∂Âêç
   * @returns {boolean} ÊòØÂê¶‰∏∫Èü≥È¢ëÊñá‰ª∂
   */
  static isAudioFile(filename) {
    const audioExtensions = ['mp3', 'wav', 'flac', 'aac', 'ogg', 'wma'];
    const extension = this.getFileExtension(filename).toLowerCase();
    return audioExtensions.includes(extension);
  }
  
  /**
   * Ê£ÄÊü•ÊòØÂê¶‰∏∫ÊñáÊ°£Êñá‰ª∂
   * @param {string} filename Êñá‰ª∂Âêç
   * @returns {boolean} ÊòØÂê¶‰∏∫ÊñáÊ°£Êñá‰ª∂
   */
  static isDocumentFile(filename) {
    const documentExtensions = ['pdf', 'doc', 'docx', 'xls', 'xlsx', 'ppt', 'pptx', 'txt'];
    const extension = this.getFileExtension(filename).toLowerCase();
    return documentExtensions.includes(extension);
  }
  
  /**
   * ÁîüÊàêÂîØ‰∏ÄÊñá‰ª∂Âêç
   * @param {string} originalName ÂéüÂßãÊñá‰ª∂Âêç
   * @returns {string} ÂîØ‰∏ÄÊñá‰ª∂Âêç
   */
  static generateUniqueFileName(originalName) {
    const timestamp = Date.now();
    const random = Math.random().toString(36).substring(2, 8);
    const extension = this.getFileExtension(originalName);
    const nameWithoutExt = this.getFileNameWithoutExtension(originalName);
    
    return `${nameWithoutExt}_${timestamp}_${random}.${extension}`;
  }
  
  /**
   * Â∞ÜÊñá‰ª∂ËΩ¨Êç¢‰∏∫Base64
   * @param {File} file Êñá‰ª∂ÂØπË±°
   * @returns {Promise<string>} Base64Â≠óÁ¨¶‰∏≤
   */
  static fileToBase64(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
  }
  
  /**
   * ‰∏ãËΩΩÊñá‰ª∂
   * @param {string} url Êñá‰ª∂URL
   * @param {string} filename Êñá‰ª∂Âêç
   */
  static downloadFile(url, filename) {
    const link = document.createElement('a');
    link.href = url;
    link.download = filename;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }
  
  /**
   * Â∞ÜBlobËΩ¨Êç¢‰∏∫Êñá‰ª∂
   * @param {Blob} blob BlobÂØπË±°
   * @param {string} filename Êñá‰ª∂Âêç
   * @returns {File} Êñá‰ª∂ÂØπË±°
   */
  static blobToFile(blob, filename) {
    return new File([blob], filename, { type: blob.type });
  }

  /**
   * Â∞ÜÊñá‰ª∂ËΩ¨Êç¢‰∏∫BlobÔºàÂÖºÂÆπFileServiceÁöÑfileToBlobÊñπÊ≥ïÔºâ
   * @param {File} file Êñá‰ª∂ÂØπË±°
   * @returns {Blob} BlobÂØπË±°
   */
  static fileToBlob(file) {
    if (file instanceof Blob) return file;
    return new Blob([file], {type: file.type});
  }

  /**
   * Ê£ÄÊü•ÂØπË±°ÊòØÂê¶‰∏∫Blob
   * @param {*} object Ë¶ÅÊ£ÄÊü•ÁöÑÂØπË±°
   * @returns {boolean} ÊòØÂê¶‰∏∫Blob
   */
  static isBlob(object) {
    return object instanceof Blob;
  }

  /**
   * ÈÄöËøáURLËé∑ÂèñBlobÂØπË±°
   * @param {string} url URLÂú∞ÂùÄ
   * @returns {Promise<Blob>} BlobÂØπË±°
   */
  static async getBlobByUrl(url) {
    if (FileUtil.isBlob(url)) {
      return url;
    }
    try {
      const response = await fetch(url);
      if (!response.ok) {
        throw new Error('Network response was not ok: ' + response.statusText);
      }
      return await response.blob();
    } catch (error) {
      console.error('Error fetching the blob:', error);
      throw error;
    }
  }

  /**
   * ‰∏ãËΩΩÊñá‰ª∂
   * @param {string|Blob} file_url Êñá‰ª∂URLÊàñBlobÂØπË±°
   * @param {string} file_name Êñá‰ª∂Âêç
   * @returns {Promise<void>}
   */
  static async download(file_url, file_name) {
    let blob = null;
    if (FileUtil.isBlob(file_url)) {
      blob = file_url;
    } else {
      blob = await FileUtil.getBlobByUrl(file_url);
    }

    const blobUrl = URL.createObjectURL(blob);

    const downloadLink = document.createElement('a');
    downloadLink.href = blobUrl;
    downloadLink.download = file_name;

    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);
    URL.revokeObjectURL(blobUrl);
  }

  /**
   * ÊâπÈáè‰∏ãËΩΩÊñá‰ª∂‰∏∫ZIPÂåÖ
   * @param {Array} files Êñá‰ª∂Êï∞ÁªÑ
   * @param {string} name ZIPÊñá‰ª∂Âêç
   * @param {string} file_url Êñá‰ª∂URLÂ≠óÊÆµÂêç
   * @param {string} save_name ‰øùÂ≠òÊñá‰ª∂ÂêçÂ≠óÊÆµÂêç
   * @returns {Promise<void>}
   */
  static async downloadAsZip(files, name = (new Date()).getTime()+'.zip', file_url = 'file_url', save_name = 'save_name') {
    const jszip = new JSZip();

    for (const file of files) {
      const blob = await FileUtil.getBlobByUrl(file[file_url]);
      jszip.file(file[save_name], blob);
    }

    try {
      const content = await jszip.generateAsync({type: "blob"});
      await FileUtil.download(content, name);
    } catch (error) {
      console.error('Error while generating zip:', error);
    }
  }
  
  /**
   * ÂéãÁº©ÂõæÁâá
   * @param {File} file ÂõæÁâáÊñá‰ª∂
   * @param {number} quality ÂéãÁº©Ë¥®Èáè (0-1)
   * @param {number} maxWidth ÊúÄÂ§ßÂÆΩÂ∫¶
   * @param {number} maxHeight ÊúÄÂ§ßÈ´òÂ∫¶
   * @returns {Promise<File>} ÂéãÁº©ÂêéÁöÑÊñá‰ª∂
   */
  static compressImage(file, quality = 0.8, maxWidth = 1920, maxHeight = 1080) {
    return new Promise((resolve) => {
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      const img = new Image();
      
      img.onload = () => {
        // ËÆ°ÁÆóÊñ∞ÁöÑÂ∞∫ÂØ∏
        let { width, height } = img;
        
        if (width > maxWidth) {
          height = (height * maxWidth) / width;
          width = maxWidth;
        }
        
        if (height > maxHeight) {
          width = (width * maxHeight) / height;
          height = maxHeight;
        }
        
        canvas.width = width;
        canvas.height = height;
        
        // ÁªòÂà∂ÂõæÁâá
        ctx.drawImage(img, 0, 0, width, height);
        
        // ËΩ¨Êç¢‰∏∫Blob
        canvas.toBlob((blob) => {
          const compressedFile = new File([blob], file.name, {
            type: file.type,
            lastModified: Date.now()
          });
          resolve(compressedFile);
        }, file.type, quality);
      };
      
      img.src = URL.createObjectURL(file);
    });
  }



}

export default FileUtil;
