<script>
import FileUtil from '@/util/FileUtil'

export default {
  name: 'VideoClipperView',
  metaInfo: {
    title: 'ËßÜÈ¢ëÊà™ÂèñÂ∑•ÂÖ∑ - ÂÖçË¥πÂú®Á∫øÂâ™ËæëMP4/MOV/WebMÔºåÁ≤æÂáÜÂà∞ÊØ´Áßí',

    meta: [
      // SEO Ê†∏ÂøÉ
      {
        name: 'description',
        content: 'ÂÖçË¥πÂú®Á∫øËßÜÈ¢ëÊà™ÂèñÂ∑•ÂÖ∑ÔºåÊîØÊåÅMP4„ÄÅMOV„ÄÅWebMÁ≠âÊ†ºÂºèÔºåÊó†ÈúÄ‰∏ä‰º†ÔºåÊµèËßàÂô®Áõ¥Êé•Á≤æÂáÜË£ÅÂâ™Âà∞ÊØ´ÁßíÔºåÊîØÊåÅËá™ÂÆö‰πâËµ∑ÂßãÊó∂Èó¥„ÄÅÊó∂ÈïøÔºå‰∏ÄÈîÆÂØºÂá∫ÔºåÊó†Ê∞¥Âç∞Êó†ÂπøÂëäÔºÅ',
        vmid: 'description'
      },
      {
        name: 'keywords',
        content: 'ËßÜÈ¢ëÊà™Âèñ,Âú®Á∫øËßÜÈ¢ëÂâ™Ëæë,ËßÜÈ¢ëË£ÅÂâ™,mp4Ââ™Ëæë,ËßÜÈ¢ëÂàáÁâá,ËßÜÈ¢ëÊèêÂèñÁâáÊÆµ,ÂÖçË¥πËßÜÈ¢ëÁºñËæë,Êó∂Èó¥ËΩ¥Ë£ÅÂâ™',
        vmid: 'keywords'
      },

      // Open GraphÔºàÂæÆ‰ø°„ÄÅQQ„ÄÅÂæÆÂçö„ÄÅÈíâÈíâÁ≠âÔºâ
      {
        property: 'og:title',
        content: 'ËßÜÈ¢ëÊà™ÂèñÂ∑•ÂÖ∑ - ÂÖçË¥πÂú®Á∫øÂâ™ËæëË£ÅÂâ™ËßÜÈ¢ë | 4YOPÂ∑•ÂÖ∑ÁÆ±',
        vmid: 'og:title'
      },
      {
        property: 'og:description',
        content: 'Êó†ÈúÄ‰∏ãËΩΩËΩØ‰ª∂ÔºåÊµèËßàÂô®Áõ¥Êé•Á≤æÂáÜÊà™ÂèñËßÜÈ¢ë‰ªªÊÑèÁâáÊÆµÔºåÊîØÊåÅÂà∞ÊØ´ÁßíÔºåÂÆåÂÖ®ÂÖçË¥πÊó†Ê∞¥Âç∞ÔºÅ',
        vmid: 'og:description'
      },
      {
        property: 'og:image',
        content: 'https://tool.4yop.com/og-video-cutter.jpg',  // Êé®Ëçê 1200√ó630 ÁöÑÊà™ÂõæÔºàÂ∏¶Êó∂Èó¥ËΩ¥+Êí≠ÊîæÊåâÈíÆÔºâ
        vmid: 'og:image'
      },
      {
        property: 'og:url',
        content: 'https://tool.4yop.com/video-cutter',
        vmid: 'og:url'
      },
      {
        property: 'og:type',
        content: 'website',
        vmid: 'og:type'
      },

      // Twitter CardÔºàÂõΩÂ§ñÂàÜ‰∫´Ôºâ
      {
        name: 'twitter:card',
        content: 'summary_large_image',
        vmid: 'twitter:card'
      },
      {
        name: 'twitter:title',
        content: 'ËßÜÈ¢ëÊà™ÂèñÂ∑•ÂÖ∑ - ÂÖçË¥πÂú®Á∫øÂâ™ËæëË£ÅÂâ™ËßÜÈ¢ë | 4YOPÂ∑•ÂÖ∑ÁÆ±',
        vmid: 'twitter:title'
      },
      {
        name: 'twitter:description',
        content: 'Êó†ÈúÄ‰∏ãËΩΩËΩØ‰ª∂ÔºåÊµèËßàÂô®Áõ¥Êé•Á≤æÂáÜÊà™ÂèñËßÜÈ¢ë‰ªªÊÑèÁâáÊÆµÔºåÊîØÊåÅÂà∞ÊØ´ÁßíÔºåÂÆåÂÖ®ÂÖçË¥πÊó†Ê∞¥Âç∞ÔºÅ',
        vmid: 'twitter:description'
      },
      {
        name: 'twitter:image',
        content: 'https://tool.4yop.com/og-video-cutter.jpg',
        vmid: 'twitter:image'
      }
    ]
  },
  data() {
    return {
      videoFile: null,
      videoUrl: '',
      videoDuration: 0,
      currentTime: 0,
      timeSegments: [
        {
          startHour: 0,
          startMinute: 0,
          startSecond: 0,
          endHour: 0,
          endMinute: 0,
          endSecond: 10
        }
      ],
      outputFormat: 'mp4',
      videoQuality: 'high',
      namingMode: 'auto',
      customPrefix: '',
      isProcessing: false,
      processingProgress: 0,
      processingText: 'ÂáÜÂ§á‰∏≠...'
    }
  },
  computed: {
    hasValidSegments() {
      return this.timeSegments.some(segment => this.isSegmentValid(segment))
    }
  },
  methods: {
    handleVideoUpload(file) {
      // Ê£ÄÊü•Êñá‰ª∂Â§ßÂ∞è (500MBÈôêÂà∂)
      if (file.size > 500 * 1024 * 1024) {
        this.$message.error('Êñá‰ª∂Â§ßÂ∞è‰∏çËÉΩË∂ÖËøá500MB')
        return false
      }

      this.videoFile = file
      this.videoUrl = URL.createObjectURL(file)
      return false // ÈòªÊ≠¢Ëá™Âä®‰∏ä‰º†
    },

    onVideoLoaded() {
      this.videoDuration = this.$refs.videoPlayer.duration
      // ËÆæÁΩÆÈªòËÆ§ÁªìÊùüÊó∂Èó¥‰∏∫ËßÜÈ¢ëÊó∂ÈïøÊàñ10Áßí
      const defaultEnd = Math.min(10, this.videoDuration)
      this.timeSegments[0].endSecond = Math.floor(defaultEnd % 60)
      this.timeSegments[0].endMinute = Math.floor(defaultEnd / 60) % 60
      this.timeSegments[0].endHour = Math.floor(defaultEnd / 3600)
    },

    onTimeUpdate() {
      this.currentTime = this.$refs.videoPlayer.currentTime
    },

    addSegment() {
      this.timeSegments.push({
        startHour: 0,
        startMinute: 0,
        startSecond: 0,
        endHour: 0,
        endMinute: 0,
        endSecond: 10
      })
    },

    removeSegment(index) {
      if (this.timeSegments.length > 1) {
        this.timeSegments.splice(index, 1)
      }
    },

    setCurrentTime(index, type) {
      const time = this.currentTime
      const hours = Math.floor(time / 3600)
      const minutes = Math.floor((time % 3600) / 60)
      const seconds = Math.floor(time % 60)

      if (type === 'start') {
        this.timeSegments[index].startHour = hours
        this.timeSegments[index].startMinute = minutes
        this.timeSegments[index].startSecond = seconds
      } else {
        this.timeSegments[index].endHour = hours
        this.timeSegments[index].endMinute = minutes
        this.timeSegments[index].endSecond = seconds
      }

      this.validateSegment(index)
    },

    validateSegment(index) {
      // Á°Æ‰øùÊó∂Èó¥ÂÄºÂú®ÊúâÊïàËåÉÂõ¥ÂÜÖ
      const segment = this.timeSegments[index]
      segment.startHour = Math.max(0, Math.min(23, segment.startHour || 0))
      segment.startMinute = Math.max(0, Math.min(59, segment.startMinute || 0))
      segment.startSecond = Math.max(0, Math.min(59, segment.startSecond || 0))
      segment.endHour = Math.max(0, Math.min(23, segment.endHour || 0))
      segment.endMinute = Math.max(0, Math.min(59, segment.endMinute || 0))
      segment.endSecond = Math.max(0, Math.min(59, segment.endSecond || 0))
    },

    isSegmentValid(segment) {
      const startTime = this.getTimeInSeconds(segment.startHour, segment.startMinute, segment.startSecond)
      const endTime = this.getTimeInSeconds(segment.endHour, segment.endMinute, segment.endSecond)
      
      return startTime < endTime && 
             startTime >= 0 && 
             endTime <= this.videoDuration &&
             (endTime - startTime) >= 1 // Ëá≥Â∞ë1Áßí
    },

    getTimeInSeconds(hours, minutes, seconds) {
      return (hours || 0) * 3600 + (minutes || 0) * 60 + (seconds || 0)
    },

    getSegmentDuration(segment) {
      if (!this.isSegmentValid(segment)) return 'Êó†Êïà'
      
      const startTime = this.getTimeInSeconds(segment.startHour, segment.startMinute, segment.startSecond)
      const endTime = this.getTimeInSeconds(segment.endHour, segment.endMinute, segment.endSecond)
      const duration = endTime - startTime
      
      return this.formatTime(duration)
    },

    previewSegment(segment) {
      if (!this.isSegmentValid(segment)) return
      
      const startTime = this.getTimeInSeconds(segment.startHour, segment.startMinute, segment.startSecond)
      this.$refs.videoPlayer.currentTime = startTime
      this.$refs.videoPlayer.play()
    },

    async startClipping() {
      if (!this.hasValidSegments) {
        this.$message.error('ËØ∑ËÆæÁΩÆÊúâÊïàÁöÑÊó∂Èó¥ÊÆµ')
        return
      }

      this.isProcessing = true
      this.processingProgress = 0
      this.processingText = 'ÂáÜÂ§áÊà™ÂèñËßÜÈ¢ë...'

      try {
        // ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®ÂÆûÈôÖÁöÑËßÜÈ¢ëÂ§ÑÁêÜAPI
        // Áî±‰∫éÊµèËßàÂô®ÈôêÂà∂ÔºåËøôÈáåÂè™ÊòØÊ®°ÊãüÂ§ÑÁêÜËøáÁ®ã
        await this.simulateVideoProcessing()
        
        this.$message.success('ËßÜÈ¢ëÊà™ÂèñÂÆåÊàêÔºÅ')
      } catch (error) {
        console.error('ËßÜÈ¢ëÊà™ÂèñÂ§±Ë¥•:', error)
        this.$message.error('ËßÜÈ¢ëÊà™ÂèñÂ§±Ë¥•ÔºåËØ∑ÈáçËØï')
      } finally {
        this.isProcessing = false
      }
    },

    async simulateVideoProcessing() {
      const validSegments = this.timeSegments.filter(segment => this.isSegmentValid(segment))
      
      for (let i = 0; i < validSegments.length; i++) {
        this.processingText = `Ê≠£Âú®Â§ÑÁêÜÁâáÊÆµ ${i + 1}/${validSegments.length}...`
        
        // Ê®°ÊãüÂ§ÑÁêÜÊó∂Èó¥
        await new Promise(resolve => setTimeout(resolve, 2000))
        
        this.processingProgress = Math.round(((i + 1) / validSegments.length) * 100)
      }
      
      this.processingText = 'Â§ÑÁêÜÂÆåÊàêÔºÅ'
    },

    resetAll() {
      this.videoFile = null
      this.videoUrl = ''
      this.videoDuration = 0
      this.currentTime = 0
      this.timeSegments = [{
        startHour: 0,
        startMinute: 0,
        startSecond: 0,
        endHour: 0,
        endMinute: 0,
        endSecond: 10
      }]
      this.outputFormat = 'mp4'
      this.videoQuality = 'high'
      this.namingMode = 'auto'
      this.customPrefix = ''
      this.isProcessing = false
      this.processingProgress = 0
      this.processingText = 'ÂáÜÂ§á‰∏≠...'
    },

    formatTime(seconds) {
      if (!seconds || seconds < 0) return '00:00:00'
      
      const hours = Math.floor(seconds / 3600)
      const minutes = Math.floor((seconds % 3600) / 60)
      const secs = Math.floor(seconds % 60)
      
      return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
    },

    formatFileSize(bytes) {
      return FileUtil.formatBytes(bytes)
    }
  }
}
</script>

